@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*, *:before, *:after{
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.container{
  max-width: 560px;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper{
  height: 100vh;
  padding-top: 60px;
  background: url(/cursor-react-hooks/static/media/wrapper-background.5a3f14af.jpg) no-repeat center / cover;
}
.contact{
    margin-bottom: 5px;
    padding: 10px 10px 5px 0;
}

.contact-inner{
    display: flex;
    flex-wrap: wrap;
}

.contact-icon{
    flex: 1 0 20%;
}

.contact-icon__image{
    max-width: 100%;
    height: auto;
    object-fit: cover;
}

.contact-info{
    flex: 1 0 80%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.contact-info__box{
    color: #fff;
    font-weight: 300;
    font-size: 1.2em;
}

.contact-phone{
    font-size: 0.9em;
}

.contact-fullname{
    margin-bottom: 5px;
    font-style: italic;
}

.first-name{
    margin-right: 5px;
}


.filter-form {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 15px;
}

.input-box {
    margin-top: 10px;
    width: 100%;
    display: flex;
    align-items: center;
}

.filter-form__input {
    width: 100%;
    padding: 10px 20px;
    border-radius: 5px;
    border: 3px solid rgba(255, 255, 255, 0.5);
    background: rgba(255, 255, 255, 0.2);
    outline: none;
    color: #fff;
    font-weight: 400;

}

.filter-form__input::-webkit-input-placeholder {
    font-style: italic;
    color: #fff;
    font-weight: 200;
    font-size: 1.2em;
}

.filter-form__input:-ms-input-placeholder {
    font-style: italic;
    color: #fff;
    font-weight: 200;
    font-size: 1.2em;
}

.filter-form__input::placeholder {
    font-style: italic;
    color: #fff;
    font-weight: 200;
    font-size: 1.2em;
}

.checkbox-block {
    margin-right: 20px;
}

.filter-form__label {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.label-img{
    width: 20px;
    height: 20px;
}

.filter-form__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.filter-form__checkbox + .filter-form__label::before {
    content: '';
    display: inline-block;
    width: 1.2em;
    height: 1.2em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #c867db;
    background:#fff;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.filter-form__checkbox:checked + .filter-form__label::before {
    border-color: #7C38C9;
    background-color: #7C38C9;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.filter-form__checkbox:not(:disabled):not(:checked) + .filter-form__label:hover::before {
    border-color: #c867db;
}

.filter-form__checkbox:not(:disabled):active + .filter-form__label::before {
    background-color: #c867db;
    border-color: #c867db;
}

.filter-form__checkbox:focus + .filter-form__label::before {
    box-shadow: 0 0 0 0.2rem rgba(124,56,201, 0.25);
}

.filter-form__checkbox:focus:not(:checked) + .filter-form__label::before {
    border-color: #c867db;
}

.filter-form__checkbox:disabled + .filter-form__label::before {
    background-color: #e9ecef;
}
.contacts-box {
    background: rgba(124, 56, 201, 0.50);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(10.5px);
    -webkit-backdrop-filter: blur(10.5px);
    border-radius: 10px;
    padding: 30px 20px 15px 20px;
}

.contacts-title {
    font-size: 1.5em;
    font-weight: 700;
    color: #222;
    line-height: 1.5;
    margin: 0 0 15px 0;
    color: #fff;
    font-weight: 400;
}

.contacts-list {
    padding: 15px;
    display: flex;
    flex-direction: column;
    max-height: 300px;
    height: 300px;
    overflow-y: auto;
}

.message{
    height: 100%;
    padding-top: 50px;
}

.message__title{
    text-align: center;
    color: #fff;
    font-size: 1.5em;
    font-weight: 400;
    font-style: italic;
}

.contacts-list::-webkit-scrollbar {
    width: 0.5em;
}

.contacts-list::-webkit-scrollbar-track {
    background: rgba(255,255,255,0.4);
    border-radius: 3px;
}

.contacts-list::-webkit-scrollbar-thumb {
    background-color: #592891;
    border-radius: 3px;
}
